            type:"map",
            map: "china",
            itemStyle: {
              areaColor: "#16425B", //区域颜色
              borderColor: "#fff",  //区域描边颜色
              emphasis:{      //鼠标悬浮区域
                areaColor: "#F59B86"
              }
            },
            label:{
              show:false,  //不显示区域名
              color:"#fff",
              fontSize: 14, 
              emphasis:{   //鼠标悬浮区域
                show: true,
                textStyle:{
                  color:"#FFF"
                }
              }
            },
          },
        ],
        visualMap:{
          type:"continuous",
          min:10,
          max:10000,
          bottom: "3%",
          left:"3%",
          inverse:false,
          orient: "horizontal",
          text: ["企业数", "底"],
          textStyle:{
            fontSize: 16,
            color:"#fff",
          },
          hoverLink: true,
          inRange: {
            color: ["#16425B", "#00E1FD"],
          }
        },
        tooltip: { //提示框
          trigger: "item",  
          formatter: "{b}<br/>企业数：{c}",
          backgroundColor: "rgba(50,50,50,0.7)", //浮窗背景色 
          borderColor: "rgba(50,50,50,0.7)",
          textStyle: {
            color:"#fff"
          }
        },
      };
      // 绘制基础图形
      this.echartsInstance.setOption(initOptions)

      // 监听用户的切换点击事件
      this.echartsInstance.on("click",async (info)=>{
        console.log(info);
        // 根据获取到的中文省份名称换取拼音字符串和省份数据地址
        let {path} = getProvinceMapInfo(info.name) 
        console.log(path);
        let {name, data} = await this.$http.get("http://localhost:8081"+path)
        //注册到echarts
        this.$echarts.registerMap(name,data) 
        // 根据新省份重新绘制地图
        this.echartsInstance.setOption({
          series:[
            {
              type:"map",
              map:name,
              zoom:0.9,
              label:{
                show:false,
                position:["50%","50%"]
              }
            }
          ]
        })
      })
    },
